export default {
	data() {
		return {
			banner:{},
			token:'',
			allNum:'',
			point_list:[],
		}
	},
	created:function(){ 
		var _this = this;
		uni.getStorage({
			 key: 'token',
			 success: function (res) {
				 _this.token = res.data
			}
		})
	},
	mounted(){
		let _this=this;
		//baner
		_this.getBanner();	
		_this.userPoint()
	},
	methods: {
		rule() {
			uni.navigateTo({
				url: "integral_rule",
			})
		},
		shop() {
			uni.navigateTo({
				url: "integral_mall",
			})
		},
		signIn() {
			uni.navigateTo({
				url: "sign_in",
			})
		},
		
		//banner
		getBanner:function(){
			let _this=this;
			let data={
				id:10,
			};
			_this.$axios(_this.$baseUrl.banner,data).then(res =>{
				if(res.data.status==1){
					_this.banner=res.data.result.banner[0];
				}
			}).catch(error =>{
				
			})
		},
		//积分记录
		userPoint:function(){
			let _this=this;
			let data={
				channel:1,
				token:_this.token,
			};
			_this.$axios(_this.$baseUrl.userPoint,data).then(res =>{
				if(res.data.status==1){
					_this.allNum = res.data.result.user_points;
					_this.point_list = res.data.result.point_list;
				}
			}).catch(error =>{
				
			})
		},
		
	}
}
